
import { mapState, mapMutations, mapActions } from 'vuex'
import sha1 from 'crypto-js/sha1'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import querystring from 'vux/src/tools/querystring/index.js'

export default {
components: {
Tab,
TabItem,
Flexbox,
FlexboxItem,
Clocker,
XButton
},
data () {
return {
statusNames: ['拼团失败', '拼团成功', '未付款', '已付款 拼团中', '已失效'],
statusBtnNames: ['再次拼团', '立即使用', '去支付', '邀请好友', '重新拼团']
}
},
computed: {
...mapState([
'pintuanMyGroup'
])
},
methods: {
...mapMutations([
'showToast'
]),
...mapActions([
'getPintuanMyGroups',
'isLogin',
'getWxTicket',
'getWxOpenId',
'h5pay',
'cancelPintuanOrder'
]),
getStatusName (status) {
return this.statusNames[status]
},
getStatusBtnName (status) {
return this.statusBtnNames[status]
},
formatDate (startDateTime, entityTimeOut) {
const endTime = startDateTime + entityTimeOut * 60 * 60 * 1000
return dateFormat(endTime, 'YYYY-MM-DD HH:mm:ss')
},
clickBtn (order) {
console.log(order)
const z = this
if (order.allStatus === '0') { // 再次拼团
z.$router.push({
name: 'PintuanProduct'
})
} else if (order.allStatus === '1') { // 立即使用
console.log('立即使用')
z.$router.push({
name: 'PintuanIntroduce'
})
} else if (order.allStatus === '2') { // 去支付

window.location.href = 'http://m.huijiacar.com/wlwc/wx-pay.html?orderId=' + order.orderId + '&wxpayReturnUri=' + encodeURIComponent('https://m.huijiacar.com/vue-mobile/#/pintuan/my')
} else if (order.allStatus === '3') { // 邀请好友
console.log('邀请好友')
sessionStorage.setItem('showShareBox', true)
z.$router.push({
name: 'PintuanProduct',
query: {
actEntityId: order.actEntityId
}
})
} else if (order.allStatus === '4') { // 重新拼团
z.$router.push({
name: 'PintuanProduct'
})
}
},
cancelOrder (orderId) {
const z = this
z.cancelPintuanOrder({
orderId: orderId
}).then(function () {
z.showToast({
type: 'success',
text: '该订单已取消'
})
z.getPintuanMyGroups()
})
},
createPay (orderId) {
const z = this
console.log(orderId)
let payData = {
orderId: orderId,
openId: z.openId,
errorNotifyUrl: '',
redirectUrl: '',
returnUrl: '',
skuId: '',
clientSystemType: '3',
payCodeEnum4H5: '1201',
paySourceEnum: '6'
}
z.h5pay(payData).then(function (obj) {
console.log(obj)
z.$wechat.chooseWXPay({
timestamp: obj.weiXinPayOutputDTO.timeStamp,
nonceStr: obj.weiXinPayOutputDTO.nonceStr,
package: obj.weiXinPayOutputDTO.wxPackage,
signType: obj.weiXinPayOutputDTO.signType,
paySign: obj.weiXinPayOutputDTO.paySign,
success: function (res) {

console.log(res)
this.$router.push({
name: 'PintuanMy'
})
}
})
})
},
randomStr (len) {
len = len || 32
const chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678' // 默认去掉了容易混淆的字符oOLl, 9gq, Vv, Uu, I1
let maxPos = chars.length
let pwd = ''
for (let i = 0; i < len; i++) {
pwd += chars.charAt(Math.floor(Math.random() * maxPos))
}
return pwd
},
initWxTicket () {
const z = this
z.getWxTicket().then(function (t) {
z.appId = t.appid
z.ticket = t.ticket
let isMicroMessenger = navigator.userAgent.toLowerCase().indexOf('MicroMessenger'.toLowerCase()) > -1
if (isMicroMessenger && !querystring.parse().code) {
let redirUri = encodeURIComponent(window.location.href)
window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + z.appId + '&redirect_uri=' + redirUri + '&response_type=code&scope=snsapi_base&state=1#wechat_redirect'
} else {
z.initWxConfig()
}
console.log(t)
}).catch(function () {

location.search = ''
})
},
initWxConfig () {
const z = this
let noncestr = z.randomStr(16)
let timestamp = new Date().getTime().toString().substr(0, 10)
let url = decodeURIComponent(location.href).split('#')[0]
let signStr = 'jsapi_ticket=' + z.ticket + '&noncestr=' + noncestr + '&timestamp=' + timestamp + '&url=' + url
let signature = sha1(signStr).toString()
z.$wechat.config({
debug: false, // 开发者工具显示详情
appId: z.appId,
timestamp: timestamp,
nonceStr: noncestr,
signature: signature,
jsApiList: [
'hideAllNonBaseMenuItem'
]
})
z.$wechat.ready(function () {
z.$wechat.hideOptionMenu()
z.$wechat.hideAllNonBaseMenuItem()
})
z.$wechat.error(function (res) {
console.log(res)
z.showToast({
type: 'warn',
text: '微信权限验证配置失败'
})
})
}
},
created () {
const z = this
z.isLogin().then(function (status) {
if (!status) {
z.$router.push({
name: 'PintuanProduct'
})
}
})
z.getPintuanMyGroups()
z.initWxTicket()
},
mounted () {
const z = this
setTimeout(function () {
z.getPintuanMyGroups()
}, 2000)








}
}



