
import { mapMutations, mapActions, mapState } from 'vuex'
import sha1 from 'crypto-js/sha1'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Clocker from 'vux/src/components/clocker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import querystring from 'vux/src/tools/querystring/index.js'


export default {
components: {
Swiper,
SwiperItem,
Flexbox,
FlexboxItem,
Clocker,
XButton,
Tab,
TabItem,
XImg,
Tabbar,
TabbarItem,
Popup,
Confirm,
XInput,
Countdown,
XDialog
},
data () {
return {
swiper: [
'~static/pintuan/banner-1.jpg',
'~static/pintuan/banner-2.jpg',
'~static/pintuan/banner-3.jpg'
],
tabIndex: 0,
showOrderPopup: false,
city: {
cityCode: 0,
cityName: ''
},
carType: '五座轿车',

carInner: false,
buyDiscount: false,
isJoin: false,
orderPrice: '',
orderInfo: {
title: '五座轿车',
categoryId: [],
orderPrice: 0,
dicountPrice: 0,
salePrice: 0
},
loginDialog: false,
loginLoading: false,
phone: '',
graphCode: '',
verifyCode: '',
graphCodeUrl: '/api/v3/portal/outward/getGraphCode',
disableGraphCode: false,
actEntityId: '',
group: {},
isUserLogin: false,
showShareBox: false,
existOrder: {},
showExistOrder: false,
appId: '',
ticket: ''
}
},
computed: {
...mapState([
'pintuanProduct'
]),
saleLowPrice: function () {
return this.pintuanProduct.saleLowPrice && (this.pintuanProduct.saleLowPrice / 100)
},
discountLowPrice: function () {
return this.pintuanProduct.discountLowPrice && (this.pintuanProduct.discountLowPrice / 100)
},
cities: function () {
let c = []
this.pintuanProduct.productList && this.pintuanProduct.productList.map(function (el) {
c.push({
cityCode: el.cityCode,
cityName: el.cityName
})
})
return c
}
},
watch: {
},
methods: {
...mapMutations([
'showToast',
'showLoading',
'hideLoading',
'setOrderInfo'
]),
...mapActions([
'sendSmsCode',
'smsLogin',
'getPintuanProduct',
'getPintuanDetails',
'getPintuanExist',
'cancelPintuanOrder',
'getWxTicket',
'getWxOpenId',
'isLogin'
]),
onTabClick: function (index) {
this.tabIndex = index
console.log(index)
},
orderPopupShow (e) {
console.log(e)
console.log('orderPopupShow')
},
orderPopupHide (e) {
console.log(e)
console.log('orderPopupHide')
},
clickOrderPopupButton (isDiscount, isJoin) {
this.showOrderPopup = true
this.buyDiscount = isDiscount
this.isJoin = isJoin
this.computeOrderPrice()
},
setCity (city) {
this.city = city
this.computeOrderPrice()
},
setCarType (type) {
this.carType = type
this.computeOrderPrice()
},
setCarInner () {
this.carInner = !this.carInner
this.computeOrderPrice()
},
computeOrderPrice () {
const z = this
let dicountPrice = 0
let salePrice = 0
if (z.city.cityCode !== 0) {
z.pintuanProduct.productList && z.pintuanProduct.productList.map(function (el) {
if (z.city.cityCode === el.cityCode) {
el.categoryList.map(function (p) {
if (p.categoryName === z.carType) {
salePrice = p.salePrice
dicountPrice = p.dicountPrice
z.orderInfo.title = z.carType
z.orderInfo.categoryId = [p.categoryid]
}
if (z.carInner && !p.main) {
salePrice += p.salePrice
dicountPrice += p.dicountPrice
z.orderInfo.title += ' + 内饰清洗'
z.orderInfo.categoryId.push(p.categoryid)
}
})
}
})
}
z.orderInfo.salePrice = salePrice / 100
z.orderInfo.dicountPrice = dicountPrice / 100
z.orderInfo.orderPrice = z.buyDiscount ? z.orderInfo.dicountPrice : z.orderInfo.salePrice
},
submit () {
const z = this
if (z.city.cityCode === 0) {
z.showToast({
type: 'warn',
text: '请选择城市'
})
} else {
let allOrderInfo = Object.assign({}, z.orderInfo, z.city)
if (z.actEntityId && z.isJoin) {
allOrderInfo.actEntityId = z.actEntityId
}
sessionStorage.setItem('pintuanOrderInfo', JSON.stringify(allOrderInfo))
z.setOrderInfo()
z.$router.push({
name: 'PintuanOrder',
query: z.$router.query
})
}
console.log('submit')
},
getGraphCode () {
this.graphCodeUrl = '/api/v3/portal/outward/getGraphCode?time=' + Number(new Date())
},
getSmsCode () {
const z = this
if (!(z.phone && z.$refs.phone.valid)) {
z.showToast({
type: 'warn',
text: '请输入正确的手机号'
})
} else if (!(z.graphCode && z.$refs.graphCode.valid)) {
z.showToast({
type: 'warn',
text: '请输入图形验证码'
})
} else {
z.sendSmsCode({
phone: z.phone,
graphCode: z.graphCode
}).then(function (status) {
z.showToast({
type: 'success',
text: '短信验证码已发送'
})
if (status === 200) {
z.disableGraphCode = true
}
})
}
},
doLogin () {
const z = this
if (!(z.phone && z.$refs.phone.valid)) {
z.showToast({
type: 'warn',
text: '请输入正确的手机号'
})
} else if (!(z.graphCode && z.$refs.graphCode.valid)) {
z.showToast({
type: 'warn',
text: '请输入图形验证码'
})
} else if (!(z.verifyCode && z.$refs.verifyCode.valid)) {
z.showToast({
type: 'warn',
text: '请输入短信验证码'
})
} else if (!z.loginLoading) {
let data = {
phone: z.phone,
verificationCode: z.verifyCode
}
z.showLoading({
text: '登陆中……'
})
z.loginLoading = true
z.smsLogin(data).then(function (status) {
z.hideLoading()
if (status === 200) {
z.loginDialog = false
z.loginLoading = false
z.$router.push({
name: 'PintuanMy'
})
}
})
}
},
clickJoin (group) {
this.clickOrderPopupButton(true, true)
console.log(group)
},
formatDate (startDateTime, entityTimeOut) {
const endTime = startDateTime + entityTimeOut * 60 * 60 * 1000
return dateFormat(endTime, 'YYYY-MM-DD HH:mm:ss')
},
clickMyBtn () {
if (this.isUserLogin) {
this.$router.push({
name: 'PintuanMy'
})
} else {
this.loginDialog = true
}
},
payExistOrder (orderId) {
window.location.href = 'http://m.huijiacar.com/wlwc/wx-pay.html?orderId=' + orderId + '&wxpayReturnUri=' + encodeURIComponent('https://m.huijiacar.com/vue-mobile/#/pintuan/my')
},
cancelExistOrder (orderId) {
const z = this
z.cancelPintuanOrder({
orderId: orderId
}).then(function () {
z.showToast({
type: 'success',
text: '该订单已取消'
})
z.existOrder = {}
})
},
checkExistOrder () {
const z = this
z.getPintuanExist({
actEntityId: z.actEntityId
}).then(function (data) {
z.existOrder = data
if (data.orderId) {
z.showExistOrder = true
}
})
},
hideShareBox () {
sessionStorage.removeItem('showShareBox')
},
randomStr (len) {
len = len || 32
const chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678' // 默认去掉了容易混淆的字符oOLl, 9gq, Vv, Uu, I1
let maxPos = chars.length
let pwd = ''
for (let i = 0; i < len; i++) {
pwd += chars.charAt(Math.floor(Math.random() * maxPos))
}
return pwd
},
initWxTicket () {
const z = this
z.getWxTicket().then(function (t) {
z.appId = t.appid
z.ticket = t.ticket
let isMicroMessenger = navigator.userAgent.toLowerCase().indexOf('MicroMessenger'.toLowerCase()) > -1
if (isMicroMessenger && !querystring.parse().code) {
let redirUri = encodeURIComponent(window.location.href)
window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + z.appId + '&redirect_uri=' + redirUri + '&response_type=code&scope=snsapi_base&state=1#wechat_redirect'
} else {
z.initWxConfig()
}
console.log(t)
}).catch(function () {


})
},
initWxConfig () {
console.log(this.$route)
const z = this
let noncestr = z.randomStr(16)
let timestamp = new Date().getTime().toString().substr(0, 10)
let url = decodeURIComponent(location.href).split('#')[0]
let signStr = 'jsapi_ticket=' + z.ticket + '&noncestr=' + noncestr + '&timestamp=' + timestamp + '&url=' + url
let signature = sha1(signStr).toString()
z.$wechat.config({
debug: false, // 开发者工具显示详情
appId: z.appId,
timestamp: timestamp,
nonceStr: noncestr,
signature: signature,
jsApiList: [
'showMenuItems',
'hideAllNonBaseMenuItem',
'onMenuShareTimeline',
'onMenuShareAppMessage'
]
})
z.$wechat.error(function (res) {
console.log(res)
z.showToast({
type: 'warn',
text: '微信权限验证配置失败'
})
})
},
initShareInfo (actEntityId) {
const z = this
const shareConfig = {
title: '慧驾邀请您参加洗车拼团',
desc: '上门洗车3人拼团15元起，拥有三项自主专利，温和去污不伤漆，超柔洁净不留痕',
link: 'https://m.huijiacar.com/vue-mobile/#/pintuan/product?actEntityId=' + actEntityId,
imgUrl: 'https://m.huijiacar.com/vue-mobile/static/pintuan/share-logo.png',
success: function () {
},
cancel: function () {
}
}
z.$wechat.ready(function () {
z.$wechat.showMenuItems({
menuList: [
'menuItem:share:appMessage',
'menuItem:share:timeline'
]
})
z.$wechat.onMenuShareTimeline(shareConfig)
z.$wechat.onMenuShareAppMessage(shareConfig)
})
}
},
created () {
const z = this
console.log(z.$route)
z.actEntityId = z.$route.query.actEntityId
console.log(z.actEntityId)
z.getPintuanProduct({
activityId: 1
})
if (z.actEntityId) {
z.getPintuanDetails({
actEntityId: z.actEntityId
}).then(function (group) {
z.group = group
})
}
z.isLogin().then(function (status) {
z.isUserLogin = status
if (status && z.actEntityId) {
z.checkExistOrder()
}
})
if (sessionStorage.getItem('showShareBox') && z.actEntityId) {
z.showShareBox = true
}
z.initWxTicket()
},
mounted () {
const z = this
if (z.$route.query.autoJoin) {
z.clickOrderPopupButton(true, true)
}
if (z.$route.query.actEntityId) {
z.initShareInfo(z.actEntityId)
} else {
z.$wechat.ready(function () {
z.$wechat.hideAllNonBaseMenuItem()
})
}
}
}



