
import { mapState, mapActions, mapMutations } from 'vuex'
import sha1 from 'crypto-js/sha1'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import querystring from 'vux/src/tools/querystring/index.js'

export default {
components: {
Flexbox,
FlexboxItem,
Group,
Cell,
XInput,
XButton,
Countdown
},
data () {
return {
orderInfo: {},
openId: '',
appId: '',
ticket: '',
payLoading: false,
phone: '',
graphCode: '',
verifyCode: '',
graphCodeUrl: '/api/v3/portal/outward/getGraphCode',
disableGraphCode: false,
isUserLogin: false,
isPaying: false
}
},
computed: {
...mapState([

])
},
methods: {
...mapMutations([
'showToast',
'showLoading',
'hideLoading'
]),
...mapActions([
'sendSmsCode',
'smsLogin',
'orderPintuan',
'isLogin',
'getWxTicket',
'getWxOpenId',
'h5pay'
]),
getGraphCode () {
this.graphCodeUrl = '/api/v3/portal/outward/getGraphCode?time=' + Number(new Date())
},
getSmsCode () {
const z = this
if (!(z.phone && z.$refs.phone.valid)) {
z.showToast({
type: 'warn',
text: '请输入正确的手机号'
})
} else if (!(z.graphCode && z.$refs.graphCode.valid)) {
z.showToast({
type: 'warn',
text: '请输入图形验证码'
})
} else {
z.sendSmsCode({
phone: z.phone,
graphCode: z.graphCode
}).then(function (status) {
z.showToast({
type: 'success',
text: '短信验证码已发送'
})
if (status === 200) {
z.disableGraphCode = true
}
})
}
},
doLogin () {
const z = this
if (!(z.phone && z.$refs.phone.valid)) {
z.showToast({
type: 'warn',
text: '请输入正确的手机号'
})
} else if (!(z.graphCode && z.$refs.graphCode.valid)) {
z.showToast({
type: 'warn',
text: '请输入图形验证码'
})
} else if (!(z.verifyCode && z.$refs.verifyCode.valid)) {
z.showToast({
type: 'warn',
text: '请输入短信验证码'
})
} else if (!z.payLoading) {
let data = {
phone: z.phone,
verificationCode: z.verifyCode
}
z.showLoading({
text: ''
})
z.payLoading = true
z.smsLogin(data).then(function (status) {
z.hideLoading()
if (status === 200) {
z.payLoading = false
z.createOrder()
}
}).catch(function () {
z.hideLoading()
z.payLoading = false
})
}
},
createOrder () {
const z = this
z.isPaying = true
z.orderPintuan({
productId: '',
productCatalog: z.orderInfo.categoryId.toString(),
saleAmonut: z.orderInfo.salePrice * 100,
introducerId: '',
actEntityId: z.orderInfo.actEntityId || '',
activityId: 2 // 淮安
}).then(function (orderId) {
z.isPaying = false

window.location.href = 'http://m.huijiacar.com/wlwc/wx-pay.html?orderId=' + orderId + '&wxpayReturnUri=' + encodeURIComponent('https://m.huijiacar.com/vue-mobile/#/pintuan-ha/my')
}).catch(function () {
z.isPaying = false
})
},
createPay (orderId) {
const z = this
console.log(orderId)
let payData = {
orderId: orderId,
openId: z.openId,
errorNotifyUrl: '',
redirectUrl: '',
returnUrl: '',
skuId: '',
clientSystemType: '3',
payCodeEnum4H5: '1201',
paySourceEnum: '6'
}
z.h5pay(payData).then(function (obj) {
console.log(obj)
z.$wechat.chooseWXPay({
timestamp: obj.weiXinPayOutputDTO.timeStamp,
nonceStr: obj.weiXinPayOutputDTO.nonceStr,
package: obj.weiXinPayOutputDTO.wxPackage,
signType: obj.weiXinPayOutputDTO.signType,
paySign: obj.weiXinPayOutputDTO.paySign,
success: function (res) {

console.log(res)
this.$router.push({
name: 'PintuanMyHa'
})
}
})
})
},
payBtn () {
const z = this
if (z.isUserLogin) {
if (!z.isPaying) {
z.createOrder()
}
} else {
z.doLogin()
}
},
randomStr (len) {
len = len || 32
const chars = 'ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678' // 默认去掉了容易混淆的字符oOLl, 9gq, Vv, Uu, I1
let maxPos = chars.length
let pwd = ''
for (let i = 0; i < len; i++) {
pwd += chars.charAt(Math.floor(Math.random() * maxPos))
}
return pwd
},
initWxTicket () {
const z = this
z.getWxTicket().then(function (t) {
z.appId = t.appid
z.ticket = t.ticket
let isMicroMessenger = navigator.userAgent.toLowerCase().indexOf('MicroMessenger'.toLowerCase()) > -1
if (isMicroMessenger && !querystring.parse().code) {
let redirUri = encodeURIComponent(window.location.href)
window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=' + z.appId + '&redirect_uri=' + redirUri + '&response_type=code&scope=snsapi_base&state=1#wechat_redirect'
} else {
z.initWxConfig()
}
console.log(t)
}).catch(function () {

location.search = ''
})
},
initWxConfig () {
const z = this
let noncestr = z.randomStr(16)
let timestamp = new Date().getTime().toString().substr(0, 10)
let url = decodeURIComponent(location.href).split('#')[0]
let signStr = 'jsapi_ticket=' + z.ticket + '&noncestr=' + noncestr + '&timestamp=' + timestamp + '&url=' + url
let signature = sha1(signStr).toString()
z.$wechat.config({
debug: false, // 开发者工具显示详情
appId: z.appId,
timestamp: timestamp,
nonceStr: noncestr,
signature: signature,
jsApiList: [
'hideAllNonBaseMenuItem'
]
})
z.$wechat.ready(function () {
z.$wechat.hideAllNonBaseMenuItem()
})
z.$wechat.error(function (res) {
console.log(res)
z.showToast({
type: 'warn',
text: '微信权限验证配置失败'
})
})
}
},
created () {
const z = this
let pintuanOrderInfo = sessionStorage.getItem('pintuanOrderInfo')
if (pintuanOrderInfo) {
z.orderInfo = JSON.parse(pintuanOrderInfo)
} else {
z.$router.push({
name: 'PintuanProductHa'
})
}
z.isLogin().then(function (status) {
z.isUserLogin = status
})
z.initWxTicket()
},
mounted () {

console.log(this.orderInfo)








}
}



